@tailwind base;
@tailwind components;

@font-face { font-family: TTMussels; src: url('../fonts/TTMusselsRegular.otf'); }
@font-face { font-family: TTMusselsBold; src: url('../fonts/TTMusselsBold.otf'); }
@font-face { font-family: TTMusselsDemiBold; src: url('../fonts/TTMusselsDemiBold.otf'); }
@font-face { font-family: AzbukaPro; src: url('../fonts/AzbukaPro.ttf'); }
@font-face { font-family: AzbukaProMedium; src: url('../fonts/AzbukaProMedium.ttf'); }
@font-face { font-family: AzbukaProBold; src: url('../fonts/AzbukaProBold.ttf'); }

body {
  font-family: 'AzbukaProMedium';
  background-color: #071B26;
  text-rendering: geometricprecision;
}

h1 {
  font-family: 'AzbukaProBold';
  font-size: 50px;
  line-height: 50px;
  text-rendering: geometricprecision;
}
h2 {
  font-family: 'TTMusselsBold';
  font-size: 36px;
  line-height: 38px;
  text-rendering: geometricprecision;
}
h3 {
  font-family: 'TTMusselsBold';
  font-size: 18px;
  line-height: 24px;
  text-rendering: geometricprecision;
}

h4 {
  font-family: 'TTMusselsBold';
  font-size: 21px;
  line-height: 23px;
  text-rendering: geometricprecision;
  text-align: center;
  color: #9EF7FA;
}
h5 {
  font-size: 14px
}
h6 {
  font-size: 12px;
}

p {
  font-size: 18px;
  line-height: 24px;
  color: #EAEBEC;
  text-rendering: geometricprecision;
}

small {
  line-height: 24px;
  color: #EAEBEC;
  text-rendering: geometricprecision;
}

.btn {
  font-family: 'TTMusselsDemiBold';
  @apply rounded-md;
}

.btn-white {
  @apply bg-white uppercase text-[#151515] btn-wide hover:bg-[#151515] hover:text-white;
}

.label-text {
  line-height: 16px;
  @apply text-[13px] text-[#707070];
}

.content {
  min-height: calc(100vh - 70px);
}

.content-landscape {
  min-height: calc(100vh - 80px);
}

.mask {
  background-color: rgba(158, 247, 250, 0.4);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  animation: animate 3s linear infinite;
  z-index: 2;
}

.mask-line {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  animation: animate 3s linear infinite;
  z-index: 2;
  border-bottom: 3px solid white;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%) ;
  transform: translateX(-50%);
}

.modal-container {
  @apply relative flex flex-col justify-center items-center border-r border-l border-[#9EF7FA];
}

.modal-container.wrong {
  @apply border-[#EF4040];
}

.modal-container.wrong h4 {
  color: #ef4040;
}

.modal-box {
  padding: 0;
  background-color: transparent;
  overflow: visible;
}

.scan {
  max-width: 280px;
  margin: 0 auto;
}

.scan svg {
  border: 20px solid rgba(0, 0, 0, 0.1) !important;
}

.bg-video video  {
  object-fit: cover;
}

.map-buttons {
  @apply flex gap-6 absolute top-[75%] right-[105px] z-20 
}

@media (max-width: 600px) {
  .map-buttons {
    @apply  top-[120%] right-[50%] translate-x-[50%]
  }
  
}

@keyframes animate {
  0% ,100% {
    height: 0;
  }
  50% {
    height: 100%;
  }
}


@tailwind utilities;